
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import backTop from '@/components/BackTop/backTop'
import ShowMore from '@/components/ShowMore/showMore'
export default{
components: {
XHeader,Collapse,ShowMore,backTop
},
mounted(){
this.params = this.$route.params;
this.getInfo();
},
data(){
return {
info:{},
params:{},
}
},
methods: {
getInfo(){

this.$ajax.post('',{
'MSH.1':'childrenExamService',
'MSH.2':"getById",
"DATA_TYPE":  this.params.DATA_TYPE,//数据类型 5：12月龄儿童健康体检记录；6：18月龄儿童健康体检记录；7：24月龄儿童健康体检记录；8：30月龄儿童健康体检记录；
'ID': this.params.ID,//ID
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res)
if(res){
this.info = res;
}
}).catch(error => {
console.log(error);

});
},
},
}




